@import '@/variables.scss';

.tab-bar {
    padding-top: 20px;
    padding-bottom: 20px;
    font-weight: bolder;
    font-size: 20px;
    text-align: center;
    cursor: pointer;
    .bar {
        padding-bottom: 10px;
    }
    .active {
        border-bottom: 5px solid $green;
    }
}
.push-box {
    background: #f1eded;
    height: 30px;
    border-radius: 13px;
    padding: 4px 0 4px 10px;
}
.active-box {
    background: #d6f5e3 !important ;
}
.number-box {
    width: 40px;
    height: 20px;
    text-align: center;
}
.box-input {
    margin-left: 100px;
}
