.campaign-container {
    margin-left: 0;
    margin-top: 65px;
}

.campaign-header {
    display: flex;
    flex-wrap: wrap;
    padding: 10px 15px;
    margin-left: -15px;
    margin-right: -15px;
    border-top: 1px solid gray;
    border-bottom: 1px solid grey;
}

.campaign-content {
    display: flex;
    flex-wrap: wrap;

    .campaign-sidebar {
        flex: 0 0 300px;
        max-width: 300px;
        border-right: 1px solid grey;
    }

    .u-email-editor {
        flex: 1;
    }
}

.fm-email-group {
    display: flex;
    margin-top: 10px;
    margin-bottom: 10px;
    padding-left: 15px;
    align-items: center;

    label {
        flex: 0 0 100px;
        max-width: 100px;
        display: block;
        margin-right: 15px;
        font-size: 16px;
    }

    .fm-input {
        flex: 1;
        display: block;
        padding: 0 15px 0 15px;
        height: 40px;
        border: 1px solid #d1dbe2;
        font-size: 15px;
        outline: none;
    }
}

.fm-email {
    margin-top: 10px;
    margin-bottom: 15px;
    align-items: center;

    label {
        display: block;
        float: none;
        margin-bottom: 10px;
    }

    .fm-input {
        display: block;
        padding: 0 15px 0 15px;
        height: 40px;
        border: 1px solid #d1dbe2;
        font-size: 15px;
        outline: none;
        width: 100%;
    }
}

.icon-list-expand {
    display: inline-block;
    background: #f1f1f1f1;
    background-repeat: no-repeat;
    height: 10px;
    width: 7px;
    transition: all 0.3s ease-in-out;
}

.campaign-menu {
    .menu-item {
        position: relative;
        border-bottom: 1px solid grey;

        .item-dropdown-list {
            overflow: hidden;
            max-height: 0;
            transition: all 0.3s ease-in-out;
            padding: 0 10px;

            .dropdown-item {
                display: flex;
                flex-wrap: wrap;
                padding: 10px 10px 10px 58px;
                text-decoration: none;
                color: grey;
                border-left: 2px solid transparent;

                input {
                    display: inline-block;
                    margin-right: 10px;
                }
            }
        }

        .item-name {
            display: flex;
            flex-wrap: wrap;
            padding: 24px 10px;
            align-items: center;
            cursor: pointer;

            .item-icon {
                flex: 0 0 25px;
                margin-right: 10px;
            }

            .item-text {
                flex: 1;
            }
        }

        &.active {
            .item-name {
                color: grey;
            }

            .icon-list-expand {
                transform: rotate(90deg);
            }

            .item-dropdown-list {
                max-height: 600px;
                overflow: initial;
            }
        }
    }
}

.code-editor,
.code-preview {
    flex: 1;
}

.code-preivew-header {
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
    .preview-icons {
        width: 30px;
        height: 30px;
        text-align: center;
        font-size: 22px;
        margin: 0 15px;
        cursor: pointer;

        i {
            line-height: 30px;
        }
    }
}

.code-preivew-body {
    border-top: 1px solid #eef4f9;
    padding: 20px;
}

.preview-mobile {
    text-align: center;
    margin: 60px auto;
    position: relative;
    width: 340px;
    height: 638px;
    padding: 55px 8px 81px;
    border-radius: 19px;
    border: 1px solid #d2d2d2;

    .mobile-speaker {
        border-radius: 14px;
        background: #d2d2d2;
        width: 70px;
        height: 4px;
        position: absolute;
        top: 26px;
        left: 155px;
    }

    .mobile-camera {
        background: #d2d2d2;
        width: 10px;
        height: 10px;
        border-radius: 13px;
        position: absolute;
        top: 23px;
        left: 132px;
    }

    .mobile-button {
        background: #d2d2d2;
        width: 51px;
        height: 51px;
        border-radius: 55px;
        position: absolute;
        top: 570px;
        left: 139px;
        cursor: pointer;
        transition: background-color 0.5s;
    }

    .mobile-preview-content {
        height: 500px;
        border: 1px solid #d2d2d2;
        overflow-y: auto;
    }
}
// html preview
.html-preview {
    border: 0;
    height: 85vh;
    padding: 0;
    margin: 0;
    width: 100%;
    max-width: 100%;
}

.mobile {
    background: url('../../../../assets/logo/mobileicon.svg');
    background-repeat: no-repeat;
}
.web {
    background: url('../../../../assets/logo/webicon.svg');
    background-repeat: no-repeat;
}
