@import 'src/variables.scss';
.header {
    top: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    padding-right: 50px;
    padding-left: 50px;
    padding-top: 15px;
    background: #ffffff;
    height: 65px;
    position: fixed;
    z-index: 35;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.38);
    border-bottom: 1px solid #e0e0e0;
}
.header-left {
    display: flex;
    font-size: 20px;
    min-width: 300px;
    height: 50px;
    .logo {
        background: url('../../assets/images/tokopedia-im-dashboard-2x.png');
        background-repeat: no-repeat;
        background-size: contain;
        width: 100%;
    }
}

.header-right {
    display: flex;
    > div {
        padding: 10px 15px 10px 15px;
    }
}

.logout {
    cursor: pointer;
}
