@import 'src/variables.scss';

.stepper {
    position: relative;
    display: flex;
    justify-content: center;
    padding-bottom: 15px;
    margin-bottom: 15px;
    .step {
        margin-left: 30px;
        margin-right: 30px;
        text-align: center;
        position: relative;

        &:not(:first-child)::before {
            content: '';
            position: absolute;
            right: 100%;
            top: 25%;
            transform: translateY(-50%);
            background-color: $grey;
            height: 1px;
            width: 60px;
        }

        .step-name {
            color: $grey;
            font-size: 12px;
        }

        .check,
        .count {
            position: relative;
            display: inline-block;
            margin-left: auto;
            margin-right: auto;
            font-size: 12px;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            margin-bottom: 12px;
            background-color: $grey;
            text-align: center;
            line-height: 24px;
            border: 1px solid transparent;
            color: $white;
        }

        .check {
            background-color: $white;
            border-color: $green;
            color: $white;

            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 4px;
                left: 8px;
                width: 6px;
                height: 12px;
                border: solid $green;
                border-width: 0 2px 2px 0;
                -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
            }
        }

        &.done,
        &.active {
            .count {
                background-color: $green;
                color: $white;
            }
            .step-name {
                color: $green;
            }
        }
    }
}
