@import url('https://fonts.googleapis.com/css?family=Open+Sans');
@import './styles/mixins';
@import './variables.scss';
* {
    &,
    &::before,
    &::after {
        box-sizing: border-box;
    }
}

img {
    max-width: 100%;
}
html {
    font-size: 100%;
}

body {
    font-size: 1rem;
    margin: 0;
    padding: 0;
    color: #ffffff;
    font-family: 'Open Sans', sans-serif;
    background-color: #ffffff;
}
a {
    text-decoration: none;
    color: #ffffff;
}
.small-button {
    width: 100% !important;
    font-size: 14px;
    border-radius: 4px;
}
.flex {
    display: flex;
}
.flex-space-bt {
    display: flex;
    justify-content: space-between;
}

.pageNotFound {
    background-image: url('./assets/images/404.jpg');
    background-repeat: no-repeat;
    height: 500px;
}

.alert {
    color: #ec6464;
    font-size: 14px;
}

.small-btn {
    width: 200px !important;
}
.float-right {
    float: right;
}
.dashboard-content {
    background: #ffffff;
    padding: 40px;
    color: $grey-text;
    &.open {
        flex: 0 0 85%;
    }
    &.close {
        flex: 0 0 95%;
    }
}
//*&*&*&*& margin classes
.mrgn-btm {
    margin-bottom: 15px;
}
.mrgn-top-15 {
    margin-top: 15px;
}
.margn-right-15 {
    margin-right: 15px;
}

.margn-left-15 {
    margin-left: 15px;
}
.mrgn-10 {
    margin: 10px;
}
//*&*&*&*& margin class end
input:focus {
    outline-color: $green;
    outline-width: 5px;
}

// grid
.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
}

.align-items-center {
    align-items: center;
}

.justify-content-center {
    justify-content: center;
}

.col {
    flex: 1;
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
}

.text-right {
    text-align: right;
}
