@import 'src/variables.scss';

.section-grid {
    display: flex;
    flex-flow: column;
    .advance-row {
        background: #fafafa;
        border: 1px solid #fafafa;
        margin: 5px 0;
        padding: 10px;
        border-radius: 4px;
        background-color: #fafafa;
        min-height: 45px;
    }
}
.row-child {
    margin-left: 40px;
    max-width: 50%;
    color: $grey-text;
    font-size: 14px;
    font-weight: 700;
}
.plus {
    margin-top: 3%;
    width: 5%;
    cursor: pointer;
}
.minus {
    color: #6cae53;
    font-size: 41px;
    cursor: pointer;
}
.input-footer {
    margin-top: -15px;
}
.divider-line {
    border-top: 2px solid #f1f1f1;
    margin: 10px 0;
}

.device-type {
    padding: 5px;
    margin-left: 5px;
    cursor: pointer;
    &.active {
        border-bottom: 3px solid $green;
    }
}
