@import 'src/variables.scss';
ul {
    list-style: none;
    padding-inline-start: 20px !important;
}
li {
    padding: 0 10px 0 10px;
}
.Wrapper {
    display: table;
    width: 100%;
    min-height: 100vh;
}
.Sidebar {
    position: relative;
    display: table-cell;
    width: 75px;
    // min-width: 216px;
    // // needs a max-width in order to overflow menu items
    // max-width: 240px;
    min-height: 100vh;
    position: absolute;
    left: 0;
    right: 0;
    height: 100%;
    transition: left 0.3s ease-in-out;
    background-image: url('../../assets/images/right-bg.png');
    background-repeat: no-repeat;
    user-select: none;
    color: black;
    transition: all 0.4s ease-in-out;

    .menu-strip {
        display: inline-block;
        position: relative;
        width: 18px;
        height: 2px;
        background-color: #ffffff;
        border-radius: 2px;

        &::before,
        &::after {
            position: absolute;
            left: 0;
            right: 0;
            content: '';
            height: 2px;
            background-color: #ffffff;
            border-radius: 2px;
        }

        &::before {
            bottom: 100%;
            margin-bottom: 3px;
        }

        &::after {
            top: 100%;
            margin-top: 3px;
        }
    }

    &.close {
        a {
            display: none;
        }
        .cross {
            display: none;
        }

        .Sidebar-navItem {
            text-align: center;
            justify-content: center;
        }
    }
    &.open {
        width: 216px;
        a {
            margin-left: 15px;
        }
        // .Sidebar-navItem {
        //     margin-right: 10px;
        //     transition: padding-left 0.4s ease;
        // }

        .Sidebar-toggleText {
            display: none;
        }

        .Sidebar-toggleArrow {
            left: 17.5px;
            transform: rotate(179deg);
        }

        .Sidebar-footer {
            &:hover {
                .Sidebar-toggleArrow {
                    left: 17.5px;
                    transition: left 0.4s ease, transform 0.4s ease;
                }
            }
        }
    }

    @media (max-width: 500px) {
        min-width: 50px;
    }
}
.Sidebar-header {
    position: relative;
    height: 130px;
    margin-bottom: 50px;
    text-align: center;
}
.Sidebar-navItem {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    white-space: nowrap;
    overflow: hidden;
    margin-top: 10px;
    transition: padding-left 0.4s ease;
    cursor: pointer;
    border-radius: 8px;
    min-height: 44px;
    @media (max-width: 500px) {
        padding-left: 17px;
    }
    &:hover {
        background-color: rgba(255, 255, 255, 0.16);
    }
    &.active {
        background-color: #ffffff;
        cursor: pointer;
        border-radius: 8px;
        margin-right: 10px;
        .home {
            background-image: url('../../assets/logo/Home/Green.svg');
            background-repeat: no-repeat;
        }
        .campaign {
            background-image: url('../../assets/logo/Campaign/Green.svg');
            background-repeat: no-repeat;
        }
        .segment {
            background-image: url('../../assets/logo/Segmentation/Green.svg');
            background-repeat: no-repeat;
        }
        .setting {
            background-image: url('../../assets/logo/Settings/Green.svg');
            background-repeat: no-repeat;
        }
        a {
            color: $green;
            font-size: 16px;
            font-weight: 600;
        }
    }
}
.Sidebar-menuIcon {
    color: $green;
    display: inline-block;
    width: 24px;
    height: 24px;
    vertical-align: middle;
}
.home {
    background-image: url('../../assets/logo/Home/White.svg');
    background-repeat: no-repeat;
}
.campaign {
    background-image: url('../../assets/logo/Campaign/White.svg');
    background-repeat: no-repeat;
}
.segment {
    background-image: url('../../assets/logo/Segmentation/White.svg');
    background-repeat: no-repeat;
}
.setting {
    background-image: url('../../assets/logo/Settings/White.svg');
    background-repeat: no-repeat;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.cross {
    cursor: pointer;
    color: #ffffff;
    padding-left: 90%;
    font-size: large;
    padding-top: 10px;
}
.sidebar-menu {
    padding: 0;
    margin: 0;
    list-style-type: none;
    padding-inline-start: 0px !important;
}
