@import 'src/variables.scss';

.section {
    border-radius: 4px;
    box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.12);
    background-color: #ffffff;
    margin-bottom: 15px;
    min-height: 118px;
    padding: 15px;
}

.section-left {
    flex: 0 0 70%;
}
.section-right {
    flex: 0 0 28%;
    min-height: 600px;
    .preview-text {
        color: rgba(0, 0, 0, 0.38);
        font-size: 12px;
    }
}
.float {
    width: 140px;
    margin: 10px;
    float: right;
    border-radius: 3px;
    display: inline-block;
    line-height: 40px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    border-radius: 4px;
    border: 1px solid #efe0e0;
}
.line {
    display: inline-block;
    color: #b7aeae;
    margin-right: 15px;
}

// ####section css
.section-grid {
    display: flex;
    flex-flow: column;
    .advance-row {
        background: #fafafa;
        border: 1px solid #fafafa;
        margin: 5px 0;
        padding: 10px;
        border-radius: 4px;
        background-color: #fafafa;
        min-height: 45px;
    }
}
.divider-line {
    border-top: 2px solid #f1f1f1;
    margin: 10px 0;
}
.dotted-line {
    border-top-style: dotted;
    margin: 10px 0;
    color: #e0e0e0;
}

.type {
    padding: 5px;
    margin-left: 5px;
    cursor: pointer;
    &.active {
        border-bottom: 3px solid $green;
    }
}

.preview-wrap {
    position: relative;
    padding: 20px;
    border-radius: 4px;
    margin-top: 15px;
    background-size: cover;
    background-repeat: no-repeat;

    .preview-content {
        background-color: #ffffff;
        padding: 15px;
    }

    .preview-title {
        font-weight: 700;
    }

    .preview-header {
        color: #bbbbbb;
        margin-bottom: 10px;
        font-size: 13px;
    }

    &.web {
        font-size: 13px;
    }

    &.android {
        background-image: linear-gradient(to bottom, #3ba5bc, #e0b6c2);
    }

    .preview-image {
        max-width: 55px;
        padding-right: 0 !important;
    }

    .pn-image {
        display: inline-block;
        background: url('../../assets/images/blank-image.svg');
        background-size: contain;
        background-repeat: no-repeat;
        height: 40px;
        width: 40px;
    }
}

.preview-container {
    position: relative;
    background-size: contain;
    background-repeat: no-repeat;
    height: 100%;
    margin-top: 40px;

    .ios {
        position: absolute;
        top: 40px;
        left: 0;
        right: 0;

        .preview-content {
            color: rgba(0, 0, 0, 0.6);
            background-color: rgba(#ffffff, 0.8);
            border-radius: 8px;
            font-size: 14px;
        }
        .preview-header {
            color: rgba(0, 0, 0, 0.6);
        }
    }

    .android-main {
        position: absolute;
        top: 40px;
        left: 0;
        right: 0;
        font-size: 14px;
        padding-left: 10px;
        padding-right: 10px;

        .preview-content {
            border-radius: 4px;
        }
    }
}

// Date Picker style

.datepicker {
    margin-bottom: 15px;
    margin-left: 25%;
    margin-top: -29px;
}

.react-datepicker__header {
    background-color: #82ce78 !important;
}

.react-datepicker__month-container {
    border: 1px solid #def1dc !important;
    margin-right: 10px !important;
    box-shadow: 0 2px 10px 0 #def1dc !important;
}

.react-datepicker__time-container
    .react-datepicker__time
    .react-datepicker__time-box
    ul.react-datepicker__time-list
    li.react-datepicker__time-list-item {
    margin-left: -50% !important;
}

.react-datepicker__time-container {
    box-shadow: 0 2px 10px 0 #def1dc !important;
}
.react-datepicker__input-container input {
    height: 40px !important;
    width: 200px !important;
    text-align: center !important;
    border-radius: 16px;
    border: 2px solid #b8d2b8 !important;
    color: #82ce78 !important;
}
