@import 'src/variables.scss';
.campaign-cards {
    position: relative;
    width: 22%;
    height: 315px;
    border-radius: 4px;
    box-shadow: $box-shadow;
    background-color: #ffffff;
    padding: 15px;
    .heading {
        font-size: 18px;
    }
    .sms {
        background: url(../../../../assets/images/sms.svg);
        background-repeat: no-repeat;
        background-size: contain;
        height: 45%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .email {
        background: url(../../../../assets/images/email.svg);
        background-repeat: no-repeat;
        background-size: contain;
        height: 45%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .popUp {
        background: url(../../../../assets/images/webPopUp.svg);
        background-repeat: no-repeat;
        background-size: contain;
        height: 45%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .pn {
        background: url(../../../../assets/images/pushNotification.svg);
        background-repeat: no-repeat;
        background-size: contain;
        height: 45%;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .discription {
        font-size: 12px;
        margin-top: 10px;
        margin-bottom: 10px;
        font-weight: normal;
        font-style: normal;
        color: rgba(0, 0, 0, 0.54);
    }
}
