@import 'src/variables.scss';

// css for content part
.content {
    position: relative;
    display: flex;
    min-height: 100vh;
    .left-content {
        flex: 0 0 50%;
        min-height: 100vh;
        @media (max-width: 1000px) {
            display: none;
        }

        .left-header {
            display: flex;
            padding-top: 25px;
            margin-left: 10%;
            width: 240px;
            height: 50px;
            .toko-im-logo {
                background: url('../../assets/images/tokopedia-im-dashboard-2x.png');
                background-repeat: no-repeat;
                background-size: contain;
                height: 100%;
                width: 100%;
            }
        }
        .content-left {
            padding: 18%;
            .im-image {
                background: url(../../assets/images/IM-Dashboard-Login-img-2x.png);
                background-repeat: no-repeat;
                height: 200px;
                width: 400px;
                background-size: cover;
            }
            .welcome {
                display: flex;
                flex-flow: column;
                padding: 0 30px;
                align-items: center;
                .wl-b {
                    font-size: 25px;
                    color: $grey-text;
                }
                .im-d {
                    font-size: 40px;
                    font-weight: bold;
                    color: $grey-text;
                }
                .learn-btn {
                    border: 1px solid #53b549;
                    text-align: center;
                    width: 190px;
                    height: 40px;
                    padding-left: 12%;
                    border-radius: 4px;
                    color: #42b549;
                    font-size: 14px;
                    padding-top: 1%;
                }
            }
        }
    }
    .right-content {
        flex: 0 0 50%;
        min-height: 100vh;
        background: url('../../assets/images/right-bg.png');
        background-size: cover;
        .right-header {
            display: flex;
            justify-content: center;
            font-size: 12px;
            font-weight: 500;
        }
        @media (max-width: 1000px) {
            flex: 0 0 100%;
        }
    }
}
.top-65 {
    top: 65px;
}
.form {
    padding: 105px 128px 100px 128px;
}
.form-content {
    width: 464px;
    min-height: 462px;
    border-radius: 16px;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.76);
    padding: 75px;
    .form-heading {
        width: 315px;
        font-size: 24px;
        text-align: center;
        color: $grey-text;
        margin-bottom: 40px;
    }
}

.header-menu {
    padding-top: 25px;
    padding-right: 10px;
    padding-left: 10px;
    color: #ffffff;
    font-size: 14px;
}

.form-input {
    height: 40px;
    width: 100%;
    border: 1px solid red;
    border-radius: 2px;
    margin-bottom: 15px;
    border: 2px solid #e0e0e0;
    padding: 10px;
    font-size: 14px;
    color: #5a5454;
}

input[type='checkbox']:checked checkbox::after {
    background-color: $green;
}

.Ingat-Saya {
    width: 60px;
    height: 20px;
    font-size: 12px;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.67;
    letter-spacing: normal;
    color: rgba(0, 0, 0, 0.24);
    margin-left: 10px;
}
.Lupa-Kata-Sandi a {
    width: 99px;
    height: 20px;
    font-size: 12px;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.67;
    letter-spacing: normal;
    text-align: right;
    color: #03ac0e;
    text-decoration: none;
}
.ggl-icon {
    margin-right: 40px;
    margin-left: 5px;
    margin-top: 5px;
    display: inline-block;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-image: url('../../assets/images/google.png');
}
.hide {
    display: none;
}
